.bx--header__menu {
  min-width: 14.5rem;
}

.bx--header__menu .bx--header__menu-item {
  height: 100%;
  padding: 0px;
  min-height: 3rem;
  color: #ffffff;
 
}

.bx--header__menu .bx--header__menu-item .iot--card {
  height: 100%;
  background-color: #000000 !important;
  min-width: 14.5rem;
  margin-bottom: 0px;
}

.bx--header__menu .bx--header__menu-item:focus,
.bx--header__menu .bx--header__menu-item:active {
  outline: 2px solid white !important;
  outline-offset: -2px;
}

.bx--header__menu .bx--header__menu-item .icon,
.bx--header__menu .bx--header__menu-item .text {
  vertical-align: middle;
  display: inline-block;
}

.bx--header__menu .bx--header__menu-item .full-name {
  white-space: nowrap;
  max-width: 170px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.bx--header__menu .bx--header__menu-item .email {
  color: #808080;
  max-width: 170px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.bx--header__menu .bx--header__menu-item .iot--card .bx--btn--secondary {
  color: #ffffff;
  border-color: #ffffff;
  background-color: #000000;
  outline-color: #000000;
  border-width: 1px;
}

.bx--header__menu .bx--header__menu-item .iot--card .bx--btn--secondary:hover, 
.bx--header__menu .bx--header__menu-item .iot--card .bx--btn--secondary:focus,
.bx--header__menu .bx--header__menu-item .iot--card .bx--btn--secondary:active {
  color: #ffffff;
  border-color: #808080;
}
.style-usericon span{
  font-weight: bold;
  margin-left: 5px;
}

.icon {
  margin-right: 5px;
}

.userProfileContent {
  margin-left: 16px; 
  padding-bottom: 10px
}

.userManageAccount {
  margin-left: 16px;
  padding-bottom: 20px;
}

//RTL Handling
html[dir='rtl'] .bx--header__menu-title[role='menuitem'][aria-expanded='true'] + .bx--header__menu {
	right: auto;
  left: 0;
}

html[dir='rtl'] .icon {
  margin-left: 5px;
}

html[dir='rtl'] .userProfileContent {
  margin-right: 16px; 
  padding-bottom: 10px;
}

html[dir='rtl'] .userManageAccount {
  margin-right: 16px;
  padding-bottom: 20px;
}

html[dir='rtl'] .style-usericon span{
  font-weight: bold;
  margin-right: 5px;
}


  
