@import "~tririga-carbon-addons-iot-react/scss/styles.scss";
@import "~carbon-components/css/carbon-components.css";

html,
body,
#root,
.app {
  height: 100%;
  width: 100%;
}

// add border for sidenav
.bx--header ~ .bx--side-nav {
  border-right: 1px solid #efefef;
}

//Collapse menu if not in hover mode and not expanded by hamburger icon
.bx--side-nav:not(.iot--side-nav--expanded):not(:hover) {

  width: 3rem;
  //Hide Scrollbar when sidenav collapsed
  ::-webkit-scrollbar{ //Chrome
    width: 0px;
    background:transparent;
  }

  //Sub menus should not display when sidenav collapsed
  .bx--side-nav__menu {
    display: none;
  }
}

//Ensure sub-menus will be able to show all expanded items.
.bx--side-nav__submenu[aria-expanded='true'] + .bx--side-nav__menu {
  max-height: 100%;
}

//Ensure any expanded sub menus display when sidenav expands.
.bx--side-nav:not(.bx--side-nav--expanded) [aria-expanded='true'] + .bx--side-nav__menu {
  height: 100%;
}
  
//Hide top level active link on hover.
.bx--side-nav:hover {
  .triMenuItemTopParentActive {
    border-left: none;
  }

  button.triMenuItemTopParentActive {
    border-left: none;
  }

  li.triMenuItemTopParentActiveBackground:not(.triMenuItemActive) {
    background-color:#393939;
  }
}

html[dir='rtl'] .bx--side-nav:hover {
  .triMenuItemTopParentActive {
    border-right: none;
  }

  button.triMenuItemTopParentActive {
    border-right: none;
  }
}

//Handle active background on hover.
li.bx--side-nav__item:hover {
  button.triMenuItemActiveBackground {
    background-color: #525252;
  }
  
  li.triMenuItemActiveBackground {
    background-color: #525252;
  }
}

//Active link handling.
.triMenuItemTopParentActive {
  border-left: 4px solid #0f62fe;
}

button.triMenuItemTopParentActive {
  border-left: 4px solid #0f62fe;
}

.triMenuItemActive {
  border-left: 4px solid #0f62fe;

  .sideNavMenuItemWithChildrenLabel{
    margin-left: -4px;
  }
}

button.triMenuItemActive {
  border-left: 4px solid #0f62fe;

  .sideNavMenuItemWithChildrenLabel{
    margin-left: -4px;
  }

  .bx--side-nav__icon {
    margin-left: -4px;
  }
}

button.triMenuItemActive span:first-child {
  margin-left: -4px;
}

html[dir='rtl'] .triMenuItemTopParentActive {
  border-right: 4px solid #0f62fe;
  border-left: none;
}

html[dir='rtl'] button.triMenuItemTopParentActive {
  border-right: 4px solid #0f62fe;
  border-left: none;
}

html[dir='rtl'] .triMenuItemActive {
  border-right: 4px solid #0f62fe;
  border-left: none;

  .sideNavMenuItemWithChildrenLabel{
    margin-right: -4px;
    margin-left: 0px;
  }
}

html[dir='rtl'] button.triMenuItemActive {
  border-right: 4px solid #0f62fe;
  border-left: none;

  .sideNavMenuItemWithChildrenLabel{
    margin-right: -4px;
    margin-left: 0px;
  }

  .bx--side-nav__icon {
    margin-right: -4px;
  }
}

html[dir='rtl'] button.triMenuItemActive span:first-child {
  margin-right: -4px;
}

button.triMenuItemActiveBackground {
  background-color: #525252;
}

li.triMenuItemActiveBackground {
  background-color: transparent;
  button.triMenuItemActiveBackground {
    background-color: #525252;
  }
}

.bx--side-nav__navigation:not(.iot--side-nav--expanded):not(:hover) {
  li.triMenuItemTopParentActiveBackground {
    background-color: #525252;
  }
}

button.triMenuItemTopParentActiveDescendantBackground:not(.triMenuItemActive) {
  background-color: #393939;
}

li.triMenuItemTopParentActiveDescendantBackground:not(.triMenuItemActive) {
  background-color: #393939;
}

//Hide top parent when menu expanded by hamburger icon.
.iot--side-nav--expanded {

  .triMenuItemTopParentActive {
    border-left: none;
  }
  
  button.triMenuItemTopParentActive {
    border-left: none;
  }

  li.triMenuItemTopParentActiveDescendantBackground {
    background-color: #393939;
  }

  li.triMenuItemTopParentActiveBackground {
    background-color:#393939;
  }
}

html[dir='rtl'] .iot--side-nav--expanded {

  .triMenuItemTopParentActive {
    border-right: none;
  }
  
  button.triMenuItemTopParentActive {
    border-right: none;
  }
}


//Hide Scrollbar when sidenav collapsed
.bx--side-nav:not(.iot--side-nav--expanded):not(:hover) ul{
  -ms-overflow-style: none; //IE & Edge
  scrollbar-width: none; //Firefox
}

.bx--side-nav__items .bx--side-nav__item > *:not([class='bx--side-nav__menu']) {
  height: 32px;
  }
  .bx--side-nav__menu-item {
  height: 32px;
  }
  .bx--side-nav__link {
  height: 32px !important;
  }

.bx--side-nav__items .bx--side-nav__item {
  position: relative;
  left: 0px;
  z-index: 1;
}

// change link content color white
.header-link {
  color: #fff;
}

//For changing the color of icon to white when downloaded from carbon design library 
.icon-color{
  filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(175deg) brightness(106%) contrast(105%); 
}

//Aligning About icon with User Profile Icon
.about-icon{
  margin-top: 3px;
}

// main container of page
.main {
  background-color: $ui-background;
  color: $text-01;
  height: 100%;
  width: 100%;

  // content under the page header
  &-content {
    padding: 0;
  }

  &-content p {
    margin-bottom: $layout-01;
  }
}

.sideNavMenuItemWithChildren {
  position: absolute;
  left: 0px; 
  bottom: 2px; 
  top: 0px; 
  z-index: 1000;
  width: 200px; 
  height: 32px; 
  padding-top: 7px;
}
//Line between side nav and header
.bx--side-nav{
  border-top-style: solid !important;
  border-top-width: 1px;
  border-top-color: #525252;
}
//TODO - Fix display issues.
html[dir='rtl'] .sideNavMenuItemWithChildren {
  position: absolute;
  right: 0px; 
  bottom: 2px; 
  top: 0px; 
  z-index: 1000;
}

.sideNavMenuItemWithChildren:focus {
  outline: 2px solid #0f62fe;
  outline-offset: -2px;
  width: 200px; 
}

.sideNavMenuItemWithChildrenLabel {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

//2-levels deep text
.bx--side-nav__item .bx--side-nav__submenu-title .sideNavMenuItemWithChildrenLabel {
  padding-left: 55px;
}

//TODO - Fix display issues.
html[dir='rtl'] .bx--side-nav__item .bx--side-nav__submenu-title .sideNavMenuItemWithChildrenLabel {
  padding-right: 55px;
  padding-left: 0px;
  text-align: right;
}

//3-levels deep text
.bx--side-nav__item .bx--side-nav__menu .bx--side-nav__item .sideNavMenuItemWithChildrenLabel {
  padding-left: 64px;
}

//TODO - Fix display issues.
html[dir='rtl'] .bx--side-nav__item .bx--side-nav__menu .bx--side-nav__item .sideNavMenuItemWithChildrenLabel {
  padding-right: 64px;
}

//4-levels deep text
.bx--side-nav__item .bx--side-nav__menu .bx--side-nav__item .bx--side-nav__menu .bx--side-nav__item .sideNavMenuItemWithChildrenLabel {
  padding-left: 80px;
}

//TODO - Fix display issues.
html[dir='rtl'] .bx--side-nav__item .bx--side-nav__menu .bx--side-nav__item .bx--side-nav__menu .bx--side-nav__item .sideNavMenuItemWithChildrenLabel {
  padding-right: 80px;
  padding-left: 0px;
}

.sitemapDiv {
  display: none;
}

//To make Landing page responsive
.main-content{
  position: relative;
  width: 100%;
  height: 100%;
}
.iot--main-content {
  transition: all 0s ease-in;
  padding-left: 48px;
  padding-right: 0px;
  height: 100%;
}

html[dir='rtl'] .iot--main-content{
  padding-right: 48px;
  padding-left:0px;
  height: 100%;
}

.projectContainerDiv{
  height: 100%;
}
.iot--side-nav--expanded + .iot--main-content {
  width: calc(100% - 13rem);
  transform: translateX(13rem);
}

//RTL pushout on hamburger icon
html[dir='rtl'] {
  .iot--side-nav--expanded + .iot--main-content {
    transform: translateX(-13rem);
  }
}

 //Setting the height of Side Nav 
.bx--side-nav__navigation
{
  max-height: calc(100vh - 3rem);
}

.bx--side-nav__link-text{
  font-weight: normal ;
}

.sideNavMenuItemWithChildrenLabel{
  font-weight: normal;
}
//This for Project Switch Button
html[dir='rtl'] .iot--btn {
  text-align: right;
  padding-left: 15px;
   padding-right: 15px; 
}
//To align side nav child to left
.bx--side-nav__item .bx--side-nav__menu .bx--side-nav__item .bx--side-nav__link-text {
  text-align: left;
}

html[dir='rtl'] .bx--side-nav__item.bx--side-nav__item--icon .bx--side-nav__link[role=menuitem] {
  padding-left: 1rem;
  padding-right: 4rem ;
}

html[dir='rtl'] .bx--side-nav__item .bx--side-nav__menu .bx--side-nav__item .bx--side-nav__menu .bx--side-nav__item .bx--side-nav__link-text {
  padding-left:  0px;
  padding-right: 30px;
}