@import '../TririgaStyles.scss';

.projects-switch-page-title {
    padding: 7px 0px 2px 14px;
    width: 100%;
    background-color: #f3f3f3;
}
.projects-switch-page-title-rtl {
  padding: 7px 14px 2px 0px;
  width: 100%;
  background-color: #f3f3f3;
}

.iot--list-item__selectable:focus,
.iot--list-item__selectable:active {
  outline: 2px solid !important;
  outline-offset: -2px;
}

.iot--list-item-parent:focus,
.iot--list-item-parent:active {
  outline: 2px solid 0f62fe;
  outline-offset: -2px;
}

.projects-switch-page-title-no-company-access {
    padding: 55px 16px 2px 18px;
    width: 100%;
    background-color: #f3f3f3;
}
//Need to check this CSS
.projects-switch-page-title-no-company-access-rtl {
  padding: 18px 2px 16px 55px;
  width: 100%;
  background-color: #f3f3f3;
}

body,
.main {
    background-color: #f3f3f3;
}

.projects-main-page {
    display: flex;
    flex-direction: column;
    flex: 0 0 100%;
  }
  
  .project-side-nav-menu {
    display: none;
  }
  
  .project-side-nav-menu.show {
    overflow: visible;
    display: block !important;
    width: 100%;
    z-index: 100;
    background-color: #ffffff;
  }
  
  .iot--list-header {
    padding: 12px 0px 12px 12px;
  }

  html[dir='rtl'] .iot--list-header {
    padding: 12px 12px 12px 0px;
  }

  .iot--list-header--title{
    white-space: nowrap;
  }
  
  .project-side-nav-menu-collapsed {
    width: 48px;
    min-height: 100%;
    background-color: #ffffff;
  }
  
  .project-side-nav-menu-collapsed.hide {
    display: none !important;
  }
  
  .project-panel-expand {
    color: #171717;
  }
  
  .projectSideNavContainer {
    display: none;
    float: left;
    margin-left: 14px;
    margin-right: 5px;
    background-color: #ffffff;
  }

  .projectSideNavContainer-rtl {
    display: none;
    float: right;
    margin-left: 5px;
    margin-right: 14px;
    background-color: #ffffff;
  }

  .recentProjectButton {
    vertical-align: middle;
    display: inline-block;
  }

  .projectActionsContainer {
    display: inline-block;
    vertical-align: middle;
  }

  .openProjectRecord {
    display: inline-block;
    vertical-align: middle;
  }

  //Make expand and collapse direction consistent with side nav.
  .iot--list-item--expand-icon {
    transform: rotateY(180deg);
  }

  html[dir='rtl'] .iot--list-header--btn-container > *{
    margin-left: 0px;
    margin-right: 0.5rem;
  }
  [dir='rtl'] .iot--btn:not(.bx--btn--icon-only) {
    text-align: right;
    padding-left: 12px;
    padding-right: 12px;
  }

  //Active link handling.
  div.projectSideNavContainer div.triProjectSubMenuItemActive {
    border-left: 4px solid #0f62fe;
    .iot--list-item--content--values--value:not(.iot--list-item--category) {
      margin-left: -4px;
    }
    .iot--list-item--expand-icon {
      margin-left: -12px;
    }
  }

  html[dir='rtl'] div.projectSideNavContainer-rtl div.triProjectSubMenuItemActive {
    border-right: 4px solid #0f62fe;
    .iot--list-item--content--values--value:not(.iot--list-item--category) {
      margin-right: -4px;
      margin-left: 0px;
    }
    .iot--list-item--expand-icon {
      margin-right: 0px;
      margin-left: 0px;
    }
  }

  div.projectSideNavContainer div.triProjectSubMenuItemClosestParentActiveBackground {
    background-color: #e5e5e5;
  }

  div.projectSideNavContainer div.triProjectSubMenuItemActiveBackground {
    background-color: #e5e5e5;
  }

  html[dir='rtl'] div.projectSideNavContainer-rtl div.triProjectSubMenuItemClosestParentActiveBackground {
    background-color: #e5e5e5;
  }

  html[dir='rtl'] div.projectSideNavContainer-rtl div.triProjectSubMenuItemActiveBackground {
    background-color: #e5e5e5;
  }

  //Normal font weight for category items.
  .iot--list-item--category {
    font-weight: normal;
  }

  //Align non-category items with category items
  .iot--list-item--content--values--value:not(.iot--list-item--category) {
    position: relative;
    padding-left: 24px;
  }

  .iot--list-item--expand-icon {
      margin-left: -8px;
  }

  html[dir='rtl'] .iot--list-item--content--values--value:not(.iot--list-item--category) {
    position: relative;
    padding-right: 41px;
    padding-left: 8px;
  }

  html[dir='rtl'] .iot--list-item--expand-icon {
      margin-right: 4px;
      margin-left: 0px;
  }

  //All the css implementation for medium breakpoint.Kept 1055px as medium breakpoint 
  //because carbon considers 1055px as medium breakpoint and hides the side nav below it.
  @media (max-width: 1055px) {
    .main {
      width: 100%;
      transform: translateX(0);
    }
    .project-landingpage {
      position: fixed;
      left: 67px;
      bottom: 0px;
      right: 0px;
      z-index: 10;
      widows: 100%;
    
    }
    .project-landingpage-rtl{
      position: fixed;
      left: 0px;
      bottom: 0px;
      right: 67px;
      z-index: 10;
      widows: 100%;
    }
    .project-side-nav-menu-collapsed{
      z-index:100;
    }


    .project-side-nav-menu.show {
      border-right: 1px solid $ui-03;
      background-color: white;
    }

    .bx--side-nav:not(.iot--side-nav--expanded):not(:hover) {
      width: 0px;
    }
    html[dir='rtl'] .iot--main-content{
      padding-right: 0px;
      padding-left:0px;
      height: 100%;
    }
    .iot--main-content {
      padding-right: 0px;
      padding-left:0px;
      height: 100%;
    }
    .iot--side-nav--expanded + .iot--main-content {
      width: calc(100% - 16rem);
      transform: translateX(16rem);
    }
    html[dir='rtl'] {
      .iot--side-nav--expanded + .iot--main-content {
        transform: translateX(-16rem);
      }
    }
  }
  
